.mainSelect>div {
  width: 105px;
  height: 60px;
  margin-left: 20px;
  color: white;
  text-align: center;
  line-height: 60px;
}

.mainSelect>div:hover {
  cursor: pointer;
  background: rgba(72, 183, 248, 0.452);
}

.select_underscore {
  border-bottom: 4px solid white;
}

.cardGroup > div {
  border: 1px solid rgba(0,0,0,.1);
}

.discordIcon:hover {
  cursor: pointer;
}

.langSelect {
  bottom: 0px;
  position: fixed;
  left: 0px;
  width: 90px;
  height: 50px;
}


