.lobby_bg {
  width: 100vw;
  height: 100vh;
  background: url('../../Asset/Lobby/bg.97a67e57.jpg');
  background-size: 100% 100%;
  text-align: center;
}

.ui-counter {
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin: 5px;
}
.ui-counter > div {
  margin: 5px;
  width: 80px;
  height: 80px;
  padding: 14px 0 8px 0;
  -webkit-box-shadow: 0 5px 10px rgba(0, 9, 128, 0.035), 0 7px 18px rgba(0, 9, 128, 0.05);
          box-shadow: 0 5px 10px rgba(0, 9, 128, 0.035), 0 7px 18px rgba(0, 9, 128, 0.05);
  background-color: rgba(255, 255, 255, 0.075);
}

#money {
  height: 36.43px; /*看字體在畫面的長度*/
  overflow: hidden;
}
#money > div {
  font-size: 32px;
  position: relative;
}

#money ul {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

#money ul li {
  height: 37px;
}

.loginBlock {
  
}

h3{
  font-size: 20px;
  margin: 15px;
  line-height: 0px;
}

.toFIFAbtn {
  margin: 0 auto;
  margin-top: 40px;
  width: 40%;
  height: 200px;
  background: rgb(95, 179, 182);
  font-size: 30px;
  line-height: 200px;
  color: white;
}

.toFIFAbtn:hover{
  cursor: pointer;
  background: rgb(114, 214, 218);
  -webkit-animation-name: '_toFIFAbtn';
          animation-name: '_toFIFAbtn';
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

@-webkit-keyframes _toFIFAbtn {
  from {background-color: rgb(95, 179, 182);}
  to {background-color: rgb(114, 214, 218);}
}

@keyframes _toFIFAbtn {
  from {background-color: rgb(95, 179, 182);}
  to {background-color: rgb(114, 214, 218);}
}
@-webkit-keyframes __toFIFAbtn {
  from {background-color: rgb(114, 214, 218);}
  to {background-color: rgb(88, 161, 163);}
}
@keyframes __toFIFAbtn {
  from {background-color: rgb(114, 214, 218);}
  to {background-color: rgb(88, 161, 163);}
}

.toFIFAbtn:active{
  -webkit-animation-name: '__toFIFAbtn';
          animation-name: '__toFIFAbtn';
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}


